body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-arrow;
  position: absolute;
  bottom: 40vmin;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-arrow;
  position: absolute;
  bottom: 40vmin;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up :first-child {
  animation: 600s steps(60, end) infinite rotate-arrow;
}

.stopwatch--speed-up > :nth-child(2) {
  animation: 10s linear infinite rotate-arrow;
}

@keyframes rotate-arrow {
  from {
    rotate: none;
  }

  to {
    transform: rotate(360deg);
  }
}

